<template>
    <div>
        <loading :active.sync="isLoading" />
        <!-- Carts -->
        <div class="my-5 row justify-content-center">
            <div class="col-md-6">
                <table class="table"">
                    <thead>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </thead>
                    <tbody>
                        <tr v-for=" item in cart.carts">
                    <td class="align-middle">
                        <button type="button" class="btn btn-outline-danger btn-sm" @click="deleteCart(item.id)">
                            <font-awesome-icon :icon="['fas', 'trash']" />
                        </button>
                    </td>
                    <td class="align-middle">
                        {{ item.product.title }}
                        <div class="text-success" v-if="item.coupon">
                            
                        </div>
                    </td>
                    <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                    <td class="align-middle text-right">{{ item.final_total | currency }}</td>
                    </tr>
                    </tbody>
                    <tfoot>
                        <tr>
                            <td colspan="3" class="text-right"></td>
                            <td class="text-right">{{ cart.total | currency }}</td>
                        </tr>
                        <tr v-if="cart.total !== cart.final_total">
                            <td colspan="3" class="text-right text-success"></td>
                            <td class="text-right text-success">{{ cart.final_total | currency }}</td>
                        </tr>
                    </tfoot>
                </table>
                <div class="input-group mb-3 input-group-sm">
                    <input type="text" class="form-control" placeholder="" v-model="coupon"
                        @keyup.enter="useCoupon">
                    <div class="input-group-append">
                        <button class="btn btn-outline-secondary" type="button" @click="useCoupon">
                            
                        </button>
                    </div>
                </div>
            </div>
        </div>
        <!--  -->
        <div class="my-5 row justify-content-center">
            <form class="col-md-6">
                <div class="form-group">
                    <label for="useremail">Email</label>
                    <input type="email" class="form-control" name="email" id="useremail" v-model="form.user.email"
                        placeholder=" Email" v-validate="'required|email'"
                        :class="{ 'is-invalid': errors.has('email') }">
                    <span class="text-danger">{{ errors.first('email') }}</span>
                </div>

                <div class="form-group">
                    <label for="username"></label>
                    <input type="text" class="form-control" name="name" id="username" v-model="form.user.name"
                        placeholder="" v-validate="'required'" :class="{ 'is-invalid': errors.has('name') }">
                    <span class="text-danger" v-if="errors.has('name')"></span>
                </div>

                <div class="form-group">
                    <label for="usertel"></label>
                    <input type="tel" class="form-control" id="usertel" name="tel" v-model="form.user.tel"
                        placeholder="" v-validate="'required'" :class="{ 'is-invalid': errors.has('tel') }">
                    <span class="text-danger" v-if="errors.has('tel')"></span>
                </div>

                <div class="form-group">
                    <label for="useraddress"></label>
                    <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
                        v-validate="'required'" placeholder="" :class="{ 'is-invalid': errors.has('address') }">
                    <span class="text-danger" v-if="errors.has('address')"></span>
                </div>

                <div class="form-group">
                    <label for="comment"></label>
                    <textarea name="" id="comment" class="form-control" cols="30" rows="10"
                        v-model="form.message"></textarea>
                </div>
                <div class="text-right">
                    <button class="btn btn-success mx-3" @click.prevent="goBack"></button>
                    <button class="btn btn-danger" @click.prevent="createOrder"></button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Carts',
    data() {
        return {
            cart: [],
            coupon: '',
            form: {
                user: {
                    name: '',
                    email: '',
                    tel: '',
                    address: '',
                },
                message: ''
            }
        }
    },
    methods: {
        addCart(id, qty = 1) {
            const vm = this;
            vm.status.cartLoading = id;
            vm.total = 0;
            const apiPath = process.env.VUE_APP_API_PATH;
            const apiCustom = process.env.VUE_APP_API_CUSTOM;
            // console.log(apiPath, apiCustom);
            vm.$http.post(`${apiPath}/api/${apiCustom}/cart`, {
                data: {
                    product_id: id,
                    qty,
                }
            })
                .then((response) => {
                    console.log(response.data);
                    $('#productItemModal').modal('hide');
                    vm.status.cartLoading = '';
                    alert(response.data.message);
                    vm.getCarts();
                });
        },
        getCarts() {
            const vm = this;
            vm.total = 0;
            const apiPath = process.env.VUE_APP_API_PATH;
            const apiCustom = process.env.VUE_APP_API_CUSTOM;
            // console.log(apiPath, apiCustom);
            vm.$http.get(`${apiPath}/api/${apiCustom}/cart`)
                .then((response) => {
                    console.log(response.data.data);
                    vm.cart = response.data.data;
                });
        },
        deleteCart(id) {
            const vm = this;
            vm.$store.dispatch('updateLoading', true);
            const apiPath = process.env.VUE_APP_API_PATH;
            const apiCustom = process.env.VUE_APP_API_CUSTOM;
            // console.log(apiPath, apiCustom);
            vm.$http.delete(`${apiPath}/api/${apiCustom}/cart/${id}`)
                .then((response) => {
                    console.log(response);
                    // alert(response.data.message);
                    vm.getCarts();
                    vm.$store.dispatch('updateLoading', false);
                });
        },
        useCoupon() {
            const vm = this;
            vm.$store.dispatch('updateLoading', true);
            const apiPath = process.env.VUE_APP_API_PATH;
            const apiCustom = process.env.VUE_APP_API_CUSTOM;
            // console.log(apiPath, apiCustom);
            vm.$http.post(`${apiPath}/api/${apiCustom}/coupon`, {
                data: {
                    code: vm.coupon
                }
            })
                .then((response) => {
                    console.log(response);
                    if (response.data.success) {
                        vm.getCarts();
                        vm.$store.dispatch('updateLoading', false);
                        vm.$bus.$emit('message:push', response.data.message, 'success');
                    } else {
                        vm.$bus.$emit('message:push', response.data.message, 'danger');
                        vm.$store.dispatch('updateLoading', false);
                    }
                    // alert(response.data.message);
                });
        },
        createOrder() {
            const vm = this;
            vm.$store.dispatch('updateLoading', true);
            const apiPath = process.env.VUE_APP_API_PATH;
            const apiCustom = process.env.VUE_APP_API_CUSTOM;
            // console.log(apiPath, apiCustom);
            const order = vm.form;
            this.$validator.validate().then(valid => {
                if (valid) {
                    vm.$http.post(`${apiPath}/api/${apiCustom}/order`, { data: { user: order.user, message: order.message } })
                        .then((response) => {
                            console.log(response);
                            if (response.data.success) {
                                // vm.$bus.$emit('message:push', response.data.message, 'success');
                                // vm.getCarts();
                                // vm.$store.dispatch('updateLoading', false);
                                vm.$router.push('customer_checkout/' + response.data.orderId);
                            } else {
                                console.log(response)
                                vm.$bus.$emit('message:push', response.data.message, 'danger');
                                // vm.getCarts();
                                vm.$store.dispatch('updateLoading', false);
                            }
                            // alert(response.data.message);
                        });
                } else {
                    vm.$bus.$emit('message:push', '', 'danger');
                    vm.$store.dispatch('updateLoading', false);
                }
            });
        },
        goBack() {
            this.$router.back();
        }
    },
    computed: {
        isLoading() {
            return this.$store.state.isLoading;
        }
    },
    created() {
        this.getCarts();
    }
}
</script>